<template>
  <div class="chat_container">
    <!-- 顶部 -->
    <van-nav-bar
      class="topbar-container"
      title="智能客服"
      left-arrow
      @click-left="$router.push(`/myInfo`)"
    />
    <!-- 消息列表 -->
    <van-cell-group ref="list" class="msg_list">
      <van-cell
        v-for="(item, index) in list"
        :class="item.id ? `left_text` : `right_text`"
        :key="index"
        :title="item.msg"
        center
      >
        <van-image
          round
          fit="cover"
          class="photo"
          :slot="item.id ? `icon` : `right-icon`"
          :src="item.id ? `https://img01.yzcdn.cn/vant/cat.jpeg` : userPhoto"
        ></van-image>
      </van-cell>
    </van-cell-group>

    <!-- 输入消息框 -->
    <van-cell-group class="sendtext_wrap">
      <van-field v-model="message" center placeholder="请输入消息...">
        <van-button @click="onsendMsg" size="small" type="primary" slot="button"
          >发送</van-button
        >
      </van-field>
    </van-cell-group>
  </div>
</template>

<script>
import io from 'socket.io-client';
import { setItem, getItem } from '@/utils/storage.js';

export default {
  name: 'UserChat',
  data() {
    return {
      list: getItem('chat_msg') || [], // 双方消息列表
      message: '', // 输入框绑定内容
      scroll_distance: 0, // 记录滚动的距离
      timer: null,
      userPhoto: getItem('userPhoto'),
    };
  },
  methods: {
    // 发送消息
    onsendMsg() {
      const data = {
        msg: this.message,
        timestamp: Date.now(),
        id: 0,
      };
      // 自定义事件发送消息给后端
      this.$store.socket.emit('sendMsg', data);

      this.list.push(data);
      this.message = '';
    },
    // 消息列表盒子自动触底
    scrollToBottom() {
      const listBox = this.$refs.list;
      listBox.scrollTop = listBox.scrollHeight;
    },
  },
  created() {
    // 链接socket地址
    this.$store.socket = io('http://192.168.0.193:3000');

    // 监听链接成功
    this.$store.socket.on('connect', () => {
      console.log('链接成功');
    });
    // 监听对方发来的消息
    this.$store.socket.on('sevMsg', (data) => {
      this.list.push(data);
    });
    // 监听服务端断开链接
    this.$store.socket.on('disconnect', () => {
      console.log('已断开连接');
    });
  },
  mounted() {
    this.scrollToBottom();
  },
  watch: {
    list() {
      setItem('chat_msg', this.list);
      this.$nextTick(() => {
        this.scrollToBottom();
      });
    },
  },
};
</script>

<style lang="less" scoped>
.msg_list {
  position: fixed;
  top: 46px;
  left: 0;
  right: 0;
  bottom: 50px;
  background-color: #f0f0f0;
  overflow-y: scroll;
  .photo {
    width: 30px;
    height: 30px;
  }
  .van-cell {
    background: none;
    span {
      border-radius: 10px;
      padding: 4px 8px;
    }
    &.left_text {
      span {
        background-color: #fff;
      }
      /deep/.photo {
        margin-right: 10px;
      }
    }
    &.right_text {
      .van-cell__title {
        text-align: right;
        span {
          background-color: rgb(114, 224, 114);
        }
      }
      .photo {
        margin-left: 10px;
      }
    }
  }
}

.sendtext_wrap {
  height: 50px;
  width: 100%;
  position: fixed;
  bottom: 0;
  display: flex;
  align-items: center;
  background-color: #f7f7f7;
  .van-field {
    padding: 0 14px;
    background-color: #f7f7f7;
  }
  .van-cell {
    /deep/.van-field__control {
      height: 40px;
      background-color: #fff;
      white-space: pre-wrap;
    }
  }
}
</style>
